angular.module('skyApp').run(['$templateCache', function($templateCache) {$templateCache.put('flights/flight-display.partial.html','<td ng-repeat="col in $columns track by $index">\n    <div class="col-md-12">\n        <div class="col-md-8">\n            <div class="line-height" ng-repeat="displayField in col.displaygroup.stats">\n                <flight-stats></flight-stats>\n            </div>\n        </div>\n        <div class="col-md-4">\n            <div class="line-height" ng-repeat="link in col.displaygroup.links">\n                <flight-downloads></flight-downloads>\n            </div>\n        </div>\n    </div>\n    <div ng-if="$last">\n        <img ng-src="{{::flight.previewUrl}}"/>\n    </div>\n</td>\n');}]);